@media (max-width: 1155px) {


  .nav-social {
    position: fixed;
    align-items: center;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 180px;
    background-color: $white;
  }
  .navbar .container {
    justify-content: space-between;
    max-height: 60px;
    .nav-logo img, .nav-logo, .nav-burger {
      max-height: 60px;

    }
  }
  .nav-burger {
    display: flex;
  }
  .nav-search {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 115px;
  }
  .nav-social {
    margin-right: 0;
  }
  .navbar.expanded {
  .container {
      height: 110vh;
      max-height: 110vh;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
    }
    .nav-links {
      position: fixed;
      margin-top: 115px;
      margin: 220px 0;
      width: 100%;
      background-color: $white;
      max-height: 100vh;
      .navbar-nav {
        align-items: center;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        overflow: scroll;
        .nav-item {
          font-size: 24px;
          &:first-child {
            display: none;
          }
        }
      }
    }
  }
  .navbar .container{
    .nav-search, .nav-social, .nav-links {
      display: none;
    }
  }
  .navbar.expanded .container{
    .nav-search, .nav-social, .nav-links {
      display: flex;
      justify-content: center;
    }
  }
}
@media (max-width: 1200px) {
    .container {
      padding: 0 15px!important;
    }
    .navbar .container {
      padding: 0!important;
    }
    .about {
      height: auto;
      padding: 10px;
      flex-direction: column;
      text-align: center;
      .btn {
        margin: auto;
      }
    }

}
