@media (max-width: 400px) {

  .news-item-lower {
    margin-left: 0;
  }
  .news-item-upper-left {
    display: none;
  }
  .news-item-upper-right.group-right {
    width: 100%;
  }
  .news-item-upper-right, .news-item-lower {
    border-left: none;
  }
  .site-logo {
    margin: auto;
  }
  .region-footer {
    flex-direction: column;
    text-align: center;
  }
  /*.navbar .form--inline {
    padding: 0;
  }
  .navbar .form-text {
    max-width: calc(100% - 40px;);
  }*/
  .news-block .form--inline{
    display: flex;
    flex-direction: column;
  }
  .news-block .form-text {
    max-width: 100%;
    width: 100%;
  }
  .news-block .form-submit {
    margin: 10px auto;
    float: none;
  }
  .news-block .bg-block {
    padding-bottom: 0;
  }
  .header {
    min-height: 600px;
    height: 100vh;
  }

  .region-search .form-submit, .region-search .form-text {
    height: 40px;
  }
  .header {
    background-position: top left;
  }
  .slogan {
    padding: 200px 0 0;
    h2 {
      font-size: 48px;
    }
    h3 {
      font-size: 36px;
    }
    p {
      font-size: 18px;
    }
  }
  .about p{
    font-size: 16px;
  }
}
